  .vimeography-thumbnail {
    margin: 0;
    max-width: 100%;
  }

  /* IE11 Support */
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    .vimeography-thumbnail {
      flex: 0 0 25%;
    }
  }

  .vimeography-link {
    display: block;
    position: relative;
    font-size: 0;
    line-height: 0;

    overflow: hidden;
    cursor: pointer;
    margin: 0 0 15px;
    box-shadow: inset 0 0 7px rgba(0, 0, 0, 0.2);
    height: 157px;
    transition: all 250ms;

    &:hover {
      box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.35);
    }
  }

  .vimeography-link-active {

  }

  .vimeography-thumbnail-overlay {
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    svg {
      transition: all 250ms;
      width: 40px;
      height: 40px;
    }

    &:hover {
      svg {
        width: 44px;
        height: 44px;
      }
    }
  }

  .vimeography-thumbnail-img {
    cursor: pointer;
    object-fit: cover;
    width: 100%;
    height: 100%;

    /* Required for inset shadow */
    position: relative;
    z-index: -1;
  }

  .vimeography-title {
    font-size: 14px;
    line-height: 1.3rem;

    text-align: left;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    color: #2a282b;
    overflow: hidden;
    background: none;
  }
